$(() => {
	
	if ( $(window).innerWidth() < 580 ) {
		var logoutClone = $('.js-logout-clone').html();
		$('.js-logout-html').html( logoutClone );
		$('.js-logout-clone').remove();
	}
	
	$('.js-select-country').focus( function() {
		$(this).closest('.input_select').addClass('is-active');
	}).change( function () {
		var value = $(this).val().toLowerCase();
		$(this).closest(".input_select").find(".input__list-item").filter(function() {
			$(this).toggle($(this).text().toLowerCase().indexOf(value) > -1)
		});
	}).keyup( function () {
		$(this).change();
	});
	
	$('.input__list-item').click(function(){
		
		var thisVal = $(this).text();
		
		if ( $(this).hasClass('is-selected') ) {
		} else {
			$('.input__list-item.is-selected').removeClass('is-selected');
			$(this).addClass('is-selected');
			$(this).closest('.input_select').find('.input__item').val( thisVal );
		}
	});
	
	$('.input__list-item, .input__bg').click(function(){
		$(this).closest('.input_select').removeClass('is-active');
	});
	
});